    background-attachment: fixed;
    background-size: cover;
}
@keyframes scroll{
    0%{
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    50%{
        -webkit-transform: translateX(calc(-360px * 6));
        transform: translateX(calc(-360px * 6));
    }
    100%{
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}